import React, {useState} from 'react'
import * as XLSX from 'xlsx'
import {saveAs} from 'file-saver'
import './estilos.css'


export const LeitorPlanilha = ()=>{

    const [data, setData] = useState([])
    const [arquivoCarregado, setArquivoCarregado] = useState(false)
    const listaMeses = ['JANEIRO', 'FEVEREIRO', 'MARÇO', 'ABRIL', 'MAIO', 'JUNHO', 'JULHO', 'AGOSTO', 'SETEMBRO', 'OUTUBRO', 'NOVEMBRO', 'DEZEMBRO']
    const mes30 = ['ABRIL', 'JUNHO', 'SETEMBRO', 'NOVEMBRO']
    const mes28 = ['FEVEREIRO']
    const unidades = [
        {
            nome: 'ETE SUL',
            intervalo: 'E5:E28',
            inicioPeriodo: 0,
            finalPeriodo: 23
        },
        {
            nome: 'ETE NORTE',
            intervalo: 'D5:D28',
            inicioPeriodo: 0,
            finalPeriodo: 23
        },
        {
            nome: 'ETE ESPERANÇA',
            intervalo: 'D6:D29',
            inicioPeriodo: 0,
            finalPeriodo: 23
        },
        {
            nome: 'ETE CAFEZAL',
            intervalo: 'C6:C15',
            inicioPeriodo: 8,
            finalPeriodo: 17
        },
        {
            nome: 'ETE SÃO LOURENÇO',
            intervalo: 'C6:C15',
            inicioPeriodo: 8,
            finalPeriodo: 17
        },
        {
            nome: 'ETE CAÇADORES',
            intervalo: 'C6:C15',
            inicioPeriodo: 8,
            finalPeriodo: 17
        }
    ]

    const handleFileUpload = (e)=>{
        if(document.getElementById('ete').value !== 'SELECIONE'){
            setArquivoCarregado(false)
            const reader = new FileReader()
            reader.readAsBinaryString(e.target.files[0])
            reader.onload = (e)=>{
                const data = e.target.result
                const dadosMes = []
                const file = XLSX.read(data, {type: 'binary'})
                let parsedData = []

                for(let i = 0; mes30.includes(listaMeses[document.getElementById('mes').value - 1]) ? i < 60 : (mes28.includes(listaMeses[document.getElementById('mes').value - 1]) ? i < 56 : i < 62) ; i += 2){
                    parsedData = XLSX.utils.sheet_to_json(file.Sheets[file.SheetNames[i]], {header: 1, range: unidades[document.getElementById('ete').value].intervalo})
                    parsedData.map((res)=>{
                        dadosMes.push(res)
                    })
                }

                setData(dadosMes)
                setArquivoCarregado(true)
            }
        }else{
            alert('Selecione uma ETE e recarregue o arquivo.')
        }
                    
    }

    const handleDownload = ()=>{
        
        if(arquivoCarregado){
            const vazoes = []
            let nAquisicao = 1
            let hora = unidades[document.getElementById('ete').value].inicioPeriodo
            let dia = 1

            if(data.length > 0){
                vazoes.push(`N§Aquisi‡Æo\t \tHorario   \tData\t \tVazao(L/s)\n`)
                
                    data.map((value)=>{
                        vazoes.push(`${nAquisicao}\t \t${hora < 10 ? '0' + hora + ':00:00' : hora + ':00:00'}\t  \t${dia < 10 ? '0' + dia : dia}/${document.getElementById('mes').value < 10 ? '0' + document.getElementById('mes').value : document.getElementById('mes').value}/${document.getElementById('ano').value}\t \t${value.length === 0 ? parseFloat(0).toFixed(0) : parseFloat(value).toFixed(0)}\n`)
                    
                        hora++

                        if(hora > unidades[document.getElementById('ete').value].finalPeriodo){
                            hora = unidades[document.getElementById('ete').value].inicioPeriodo
                            dia++
                        }

                        if(dia > 31){
                            dia = 1
                        }

                        nAquisicao++
                    })     
            }

            const file = new Blob(vazoes, {type: 'text/plain;charset=utf-8'})
            saveAs(file, `${unidades[document.getElementById('ete').value].nome.replace(' ', '-')}-${listaMeses[document.getElementById('mes').value-1]}-${document.getElementById('ano').value}.txt`)
        }else{
            alert('Todos os campos devem ser preenchidos.')
        }

        
    }
    
    return(
        <div className='janela'>
            <div className='container'>

                <div className='grupo-select'>
                    <label>ETE: </label>
                    <select id='ete'>
                        <option value={null}>SELECIONE</option>
                        {
                            unidades.map((unidade, index)=>(
                                <option value={index}>{unidade.nome}</option>
                            ))
                        }
                    </select>
                </div>

                <div className='grupo-select'>
                    <label>Mês: </label>
                    <select id='mes'>
                        <option value={null}>SELECIONE</option>
                        {
                            listaMeses.map((mes, index)=>(
                                <option value={index+1}>{mes}</option>
                            ))
                        }
                    </select>
                </div>
                
                <div className='grupo-input-ano'>
                    <label>Ano: </label>
                    <input id='ano' type='text' placeholder='Informe o ano'/>
                </div>
                

                <input
                    type='file'
                    accept='.xlsx, .xls'
                    onChange={handleFileUpload}
                    className='input-document'
                />

                {
                    arquivoCarregado
                    ?
                    <text>{`Arquivo pronto para baixar!`}</text>
                    :
                    null
                }

                <button onClick={handleDownload}>Baixar arquivo txt</button>
                
            </div>
        </div>
        
    )
}